}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: unset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

/*Добавление совместных блоков*/
.nojs.header {
  position: static;
  height: 325px;
  flex-direction: column;
  background-color: #273544;
  padding: 0;
}

.nojs .header__logo {
  margin: 22px 0 0 20px;
