.header {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 66px;
  box-sizing: border-box;
  z-index: 100;
  padding: 0 20px;
  border-bottom: 1px solid transparent;
}
@media (min-width: 660px) {
  .header {
    height: 110px;
  }
}
@media (min-width: 1200px) {
  .header {
    max-width: 1200px;
    background-color: transparent;
    align-self: center;
    padding: 0 130px;
  }
}

@media (min-width: 1200px) {
  .header::before,
.header::after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100vw;
    z-index: -1;
  }
}

@media (min-width: 1200px) {
  .header::before {
    left: calc((100vw - 600px) * -1);
  }
}

@media (min-width: 1200px) {
  .header::after {
    right: calc((100vw - 600px) * -1);
  }
}

